/**
 *  Unity Time 
 * 
 * 1. deltaTime timeScale 
 * 2. unscaledDeltaTime timeScale 
 * 3. time timeScale 
 * 4. fixedTime/
 * 5. timeScale0=1=>1=<1=
 */
export class Time {
    // ==========================  Unity Time ==========================
    /**  timeScale  0 */
    public static deltaTime: number = 0;

    /**  timeScale  */
    public static unscaledDeltaTime: number = 0;

    /**  timeScale  */
    public static time: number = 0;

    /**  timeScale  */
    public static unscaledTime: number = 0;

    /** / 0.02  = 50 / Unity fixedDeltaTime */
    public static fixedDeltaTime: number = 0.02;

    /**  Unity fixedTime */
    public static fixedTime: number = 0;

    /** 0 = 1 = 2 = 20.5 = 0.5 */
    public static timeScale: number = 1;

    /**  30/60  */
    public static maxFps: number | null = null;

    // ========================== ==========================
    /**  */
    private static lastFrameTime: number = performance.now();

    /**  */
    private static fixedTimeRemainder: number = 0;

    /**  */
    private static isInitialized: boolean = false;


    // ========================== ==========================
    /**
     *  Unity 
     * @returns 
     */
    public static updateFrame(): boolean {
        // 
        if (!this.isInitialized) {
            this.lastFrameTime = performance.now();
            this.isInitialized = true;
            return true;
        }

        // 1. 
        const currentTime = performance.now();
        const rawDeltaMs = currentTime - this.lastFrameTime;
        this.unscaledDeltaTime = rawDeltaMs / 1000; //  timeScale 

        // 2.  maxFps
        if (this.maxFps !== null) {
            const targetFrameMs = 1000 / this.maxFps; // 
            if (rawDeltaMs < targetFrameMs) {
                return false; // 
            }
        }

        // 3.  timeScale 
        this.deltaTime = this.unscaledDeltaTime * this.timeScale; // 
        this.time += this.deltaTime; // 
        this.unscaledTime += this.unscaledDeltaTime; // 

        // 4. /
        this.updateFixedTime();

        // 5. 
        this.lastFrameTime = currentTime;
        return true; // 
    }

    /**
     *  Unity FixedUpdate
     * @param fixedUpdateCallback  fixedDeltaTime 
     */
    public static updateFixedTime(fixedUpdateCallback?: () => void): void {
        //  timeScale 
        const totalFixedDelta = this.unscaledDeltaTime + this.fixedTimeRemainder;
        const fixedStep = this.fixedDeltaTime;

        //  0.05  0.02    2  0.01 
        const fixedUpdateCount = Math.floor(totalFixedDelta / fixedStep);
        this.fixedTimeRemainder = totalFixedDelta % fixedStep;

        // 
        for (let i = 0; i < fixedUpdateCount; i++) {
            this.fixedTime += fixedStep; // 
            fixedUpdateCallback?.();
        }
    }

    /**
     * 
     */
    public static reset(): void {
        this.deltaTime = 0;
        this.unscaledDeltaTime = 0;
        this.time = 0;
        this.unscaledTime = 0;
        this.fixedTime = 0;
        this.fixedTimeRemainder = 0;
        this.lastFrameTime = performance.now();
    }

    /**
     *  timeScale = 0
     */
    public static pause(): void {
        this.timeScale = 0;
    }

    /**
     *  timeScale = 1
     */
    public static resume(): void {
        this.timeScale = 1;
    }
}